.wamidh {
  z-index: 9999;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  animation: wamidh 1.5s infinite;
  display: block;
}

@keyframes wamidh {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 0;
  }
}
.nextSalatActiveH {
  padding: 0px 0.35em;
  background-color: white;
  color: #06402b;
  border-radius: 28px;
  padding: 0.1em 0.9em;
}

.nextSalatActiveV {
  padding: 0px 0.35em;
  background-color: white;
  color: #06402b;
  border-radius: 28px;
  padding: 0.5em 0.1em;
}

.salettd {
  padding: 0px 0.35em;
}

@media screen and (max-width: 1200px) {
  .salettdV {
    padding: 0px 0.5%;
  }
}
@media screen and (min-width: 1300px) {
  .salettdV {
    padding: 0px 3%;
  }
}

.blackScreen {
  width: 100%;
  top: 0;
  left: 0;
  height: 100vh;
  color: white;
  position: fixed;
  z-index: 999;
  display: none;
  justify-content: center;
  background-color: black;
  align-items: center;
  font-size: 10em;
